<template>
  <div class=" main_cont">
    <Header></Header>
      <Sidebar></Sidebar>
    <div class="content">
      <div class="create_btn">
        <router-link to="/category/create" class="btn btn-primary">Create</router-link>
      </div>
      <table class="admin-table">
        <thead>
        <tr>
          <th>Id</th>
          <th>Name</th>
          <th>Actions</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="item in datas" :key="item">
          <td>{{item.id}}</td>
          <td>{{item.name}}</td>
          <td>
            <button class="delete-button" @click="deleteGrop(item)">Delete</button>
          </td>
        </tr>
        <!-- Add more rows dynamically or manually as needed -->
        </tbody>
      </table>
    </div>
  </div>
</template>
<script>
import Header from "../components/Header";
import Sidebar from "../components/Sidebar";
export default {
  components: {Sidebar, Header},
  data(){
    return{
      datas:[]
    }
  },
  mounted() {
    this.$axiosDefault.get('category',
        {
          headers: {
            'Accept-Language': 'oz',
            'Authorization':  `Bearer  ${localStorage.getItem('_toke')}`,
          }
        })
        .then(res=>{
          this.datas=res.data
          console.log(this.datas)
        })
  },
  methods:{
    deleteGrop(item){
      this.$axiosDefault.delete(`category/delete/${item.id}`,
          {
            headers: {
              'Accept-Language': 'oz',
              'Authorization':  `Bearer  ${localStorage.getItem('_toke')}`,
            }
          })
          .then(()=>{
            this.$axiosDefault.get(`category`,
                {
                  headers: {
                    'Accept-Language': 'oz',
                    'Authorization':  `Bearer  ${localStorage.getItem('_toke')}`,
                  }
                })
                .then(res=>{
                  this.datas=res.data
                })
          })
    }
  }
}
</script>
